				"html": "<p>Assignment operators, as the name suggests, assign (or re-assign) values to a variable. While there are quite a few variations on the assignment operators, they all build off of the basic assignment operator.</p>\n<h2>Syntax</h2>\n<pre><code>x = y;\n\n      | Description                                   | Necessity\n</code></pre>\n<p>--------- | --------------------------------------------- | ---------<br>\nx | Variable | Required<br>\n= | Assignment operator | Required<br>\ny | Value to assign to variable | Required</p>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Assignment_Operators#Assignment\">MDN link</a><br>\n<a href=\"https://msdn.microsoft.com/en-us/library/1w2h1k9x(v=vs.94).aspx\">MSDN link</a></p>\n<h2>Examples</h2>\n<pre><code>let initialVar = 5;   // Variable initialization requires the use of an assignment operator\n\nlet newVar = 5;\nnewVar = 6;   // Variable values can be modified using an assignment operator\n</code></pre>\n<h2>Variations</h2>\n<p>The other assignment operators are a shorthand for performing some operation using the variable (indicated by x above) and value (indicated by y above) and then assinging the result to the variable itself.</p>\n<p>For example, below is the syntax for the addition assignment operator:</p>\n<pre><code>x += y;\n</code></pre>\n<p>This is the same as applying the addition operator and reassigning the sum to the original variable (i.e., x), which can be expressed by the following code:</p>\n<pre><code>x = x + y;\n</code></pre>\n<p>To illustrate this using actual values, here is another example of using the addition assignment operator:</p>\n<pre><code>let myVar = 5;   // value of myVar: 5\nmyVar += 7;   // value of myVar: 12 = 5 + 7\n</code></pre>\n<h2>Complete list of Javascript's assignment operators</h2>\n<table>\n<thead>\n<tr>\n<th>Operator</th>\n<th>Syntax</th>\n<th>Long version</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Assignment</td>\n<td>x = y</td>\n<td>x = y</td>\n</tr>\n<tr>\n<td>Addition assignment</td>\n<td>x += y</td>\n<td>x = x + y</td>\n</tr>\n<tr>\n<td>Subtraction assignment</td>\n<td>x -= y</td>\n<td>x = x - y</td>\n</tr>\n<tr>\n<td>Multiplication assignment</td>\n<td>x *= y</td>\n<td>x = x * y</td>\n</tr>\n<tr>\n<td>Division assignment</td>\n<td>x /= y</td>\n<td>x = x / y</td>\n</tr>\n<tr>\n<td>Remainder assignment</td>\n<td>x %= y</td>\n<td>x = x % y</td>\n</tr>\n<tr>\n<td>Exponentiation assignment</td>\n<td>x **= y</td>\n<td>x = x ** y</td>\n</tr>\n<tr>\n<td>Left shift assignment</td>\n<td>x &#x3C;&#x3C;= y</td>\n<td>x = x &#x3C;&#x3C; y</td>\n</tr>\n<tr>\n<td>Right shift assignment</td>\n<td>x >>= y</td>\n<td>x = x >> y</td>\n</tr>\n<tr>\n<td>Unsigned right shift assignment</td>\n<td>x >>>= y</td>\n<td>x = x >>> y</td>\n</tr>\n<tr>\n<td>Bitwise AND assignment</td>\n<td>x &#x26;= y</td>\n<td>x = x &#x26; y</td>\n</tr>\n<tr>\n<td>Bitwise XOR assignment</td>\n<td>x ^= y</td>\n<td>x = x ^ y</td>\n</tr>\n<tr>\n<td>Bitwise OR assignment</td>\n<td>x</td>\n<td>= y</td>\n</tr>\n</tbody>\n</table>",
